import React, { Component } from 'react'
import { connect } from 'react-redux'
import { getForumposts } from '../reducers/forumposts'
import ForumpostLink from './ForumpostLink'

class Category extends Component {

    constructor(props) {
        super(props)
        this.state = {
        }
    }

    componentWillMount = async () => {
        if (this.props.categoryid !== undefined) {
            await this.props.getForumposts(this.props.categoryid)
        }
    }

    render() {
        if (this.props.forumposts === undefined) {
            return <div />
        }
        //"/category/:categoryid/forumpost/:forumpostid"
        

        return (
            <div>
                <ul>
                    {this.props.forumposts.map(post =>
                        <ForumpostLink key={post.forumpostid} forumpost={post} />
                    )}
                </ul>
            </div>
        )
    }
}
const mapStateToProps = (state) => {
    return {
        categories: state.categories,
        forumposts: state.forumposts
    }
}
export default connect(
    mapStateToProps,
    { getForumposts }

)(Category)


